import { useCallback } from 'react';
import { get } from 'lodash';
import { useGasPrice } from '../state/user/hooks';
/**
 * Perform a contract call with a gas price returned from useGasPrice
 * @param contract Used to perform the call
 * @param methodName The name of the method called
 * @param methodArgs An array of arguments to pass to the method
 * @param overrides An overrides object to pass to the method. gasPrice passed in here will take priority over the price returned by useGasPrice
 * @returns https://docs.ethers.io/v5/api/providers/types/#providers-TransactionReceipt
 */
export function useCallWithGasPrice() {
    const gasPrice = useGasPrice();
    const callWithGasPrice = useCallback(async (contract, methodName, methodArgs = [], overrides = null) => {
        const contractMethod = get(contract, methodName);
        const hasManualGasPriceOverride = overrides === null || overrides === undefined ? undefined : overrides.gasPrice;
        const tx = await contractMethod(...methodArgs, hasManualGasPriceOverride ? ({ ...overrides}) : ({...overrides, gasPrice}));
        return tx;
    }, [gasPrice]);
    return { callWithGasPrice };
}