import PropTypes from 'prop-types'
import {
    Button,
    Dialog,
    DialogContent,
    DialogTitle
} from "@material-ui/core";
import {useMemo, useState} from "react";
import {ethers} from "ethers";
import InputField from "./InputField";
import useActiveWeb3React from "../../../hooks/useWeb3";
import {useAppDispatch} from "../../../redux/store";
import {showError} from "../../../config/helpers";
import useStakePool from "../hooks/useStakePool";
import useUnstakePool from "../hooks/useUnstakePool";
import {getFullDisplayBalance} from "../../../utils/formatBalance";

const StakeAction = ({
    isBnbPool,
    pool,
    stakingTokenBalance,
    isDisabled,
    isUnstake
}) => {
    const { sousId, stakingToken, earningTokenPrice, apr, userData, stakingLimit, earningToken } = pool
    const [open, setOpen] = useState(false)
    const [input, setInput] = useState(false)
    const { onStake } = useStakePool(sousId, isBnbPool)
    const { onUnstake } = useUnstakePool(sousId, pool.enableEmergencyWithdraw)
    const { account } = useActiveWeb3React()
    const dispatch = useAppDispatch()

    const fullBalance = useMemo(() => getFullDisplayBalance(stakingTokenBalance), [stakingTokenBalance])

    const handleClose = () => {
        setOpen(false)
    }

    const openDialog = () => {
        setInput("")
        setOpen(true)
    }

    const handleInput = (val) => {
        setInput(val)
    }

    const handleMax = () => {
        setInput(fullBalance)
    }

    const handleStake = async (amount) => {
        try {
            await onStake(amount)
        }catch (e) {
            showError(e.data ? e.data.message : e.message)
            console.log(e)
            return null
        }
    }

    const handleUnstake = async (amount) => {
        try{
            await onUnstake(amount)
        }catch (e) {
            showError(e.data ? e.data.message : e.message)
            console.log(e)
            return null
        }
    }

    const onDismiss = () => {
        setOpen(false)
    }

    return (
        <>
            <button variant="contained" color="primary" disabled={isDisabled} onClick={openDialog} className="px-2 md:px-4 py-2 md:py-3 text-center text-xs md:text-sm text-white bg-sky-600 rounded-lg font-medium hover:bg-blue-600 transition-all disabled:bg-opacity-50">
                {
                    isUnstake ?
                        "Unstake"
                        : "Stake"
                }
            </button>
            <Dialog open={open} onClose={handleClose}>
                <DialogTitle>
                    {
                        isUnstake ?
                            "Unstake"
                            : "Stake"
                    } {stakingToken.symbol}
                </DialogTitle>
                <DialogContent dividers>
                    <InputField
                        tokenSymbol={stakingToken.symbol}
                        selectedToken={stakingToken.address}
                        tokenBalance={stakingTokenBalance}
                        onInputChange={handleInput}
                        inputValue={input}
                        max={stakingTokenBalance}
                        onMax={handleMax}
                        onConfirm={isUnstake ? handleUnstake : handleStake}
                        onDismiss={onDismiss}
                    />
                </DialogContent>
            </Dialog>
        </>
    )
}

StakeAction.propTypes = {
    isUnstake: PropTypes.bool,
    isDisabled: PropTypes.bool,
    pool: PropTypes.any,
    stakingTokenBalance: PropTypes.any,
    isBnbPool: PropTypes.bool
}

export default StakeAction

