import PropTypes from 'prop-types'
import {Button} from "@material-ui/core";
import {useState} from "react";
import useHarvestPool from "../hooks/useHarvestPool";
import {showError, showFulfill} from "../../../config/helpers";

function HarvestAction({
    pool,
    hasEarnings,
    needsApproval,
    isBnbPool,
    isDisabled=false
    }) {
    const { sousId } = pool
    const { onReward } = useHarvestPool(sousId, isBnbPool)
    const [pendingTx, setPendingTx] = useState(false)

    const onHarvest = async () => {
        setPendingTx(true)
        try {
            await onReward()
            // showFulfill('Successfully Harvested')
            setPendingTx(false)
        } catch (e) {
            showError('Please try again. Confirm the transaction and make sure you are paying enough gas!')
            setPendingTx(false)
        }
    }
    return (
        <button variant="contained" disabled={(!hasEarnings || needsApproval || pendingTx) || isDisabled} onClick={onHarvest} className="px-2 md:px-4 py-2 md:py-3 text-center text-xs md:text-sm text-white bg-sky-600 rounded-lg font-medium hover:bg-blue-600 transition-all disabled:bg-opacity-50" >
            Harvest
        </button>
    )
}

HarvestAction.propTypes = {
    pool: PropTypes.any,
    hasEarnings: PropTypes.bool,
    needsApproval: PropTypes.bool,
    isBnbPool: PropTypes.bool,
    isDisabled: PropTypes.bool,
}

export default HarvestAction