import PropTypes from 'prop-types'
import {Button} from "@material-ui/core";
import {useState} from "react";
import useHarvestPool from "../hooks/useHarvestPool";
import {showError, showFulfill} from "../../../config/helpers";
import useStakePool from "../hooks/useStakePool";

function CompoundAction({
    pool,
    hasEarnings,
    needsApproval,
    isBnbPool,
    fullBalance,
    isDisabled=false
}) {
    const { sousId, earningToken } = pool
    const { onStake } = useStakePool(sousId, isBnbPool)
    const [pendingTx, setPendingTx] = useState(false)

    const onCompound = async () => {
        setPendingTx(true)
        try {
            await onStake(fullBalance, earningToken.decimals, true)
            // showFulfill('Successfully Compound Rewards')
            setPendingTx(false)
        } catch (e) {
            showError('Please try again. Confirm the transaction and make sure you are paying enough gas!')
            setPendingTx(false)
        }
    }
    return (
        <button variant="contained" disabled={!hasEarnings || needsApproval || pendingTx || isDisabled} onClick={onCompound} fullWidth className="px-2 md:px-4 py-2 md:py-3 mt-4 mx-auto table text-center text-xs md:text-sm text-black bg-white rounded-lg font-medium hover:bg-sky-600 hover:text-white transition-all disabled:bg-opacity-50" >
            Compound Rewards
        </button>
    )
}

CompoundAction.propTypes = {
    pool: PropTypes.any,
    hasEarnings: PropTypes.bool,
    needsApproval: PropTypes.bool,
    isBnbPool: PropTypes.bool,
    isDisabled: PropTypes.bool,
    fullBalance: PropTypes.any
}

export default CompoundAction