import { useEffect, useMemo } from 'react';
import BigNumber from 'bignumber.js';
import { useWeb3React } from '@web3-react/core';
import { useSelector } from 'react-redux';
import { useAppDispatch } from '../../redux/store';
import { simpleRpcProvider } from '../../utils/providers';
import useRefresh from '../../hooks/useRefresh';
import {fetchPoolsPublicDataAsync, fetchPoolsStakingLimitsAsync, fetchPoolsUserDataAsync} from '.';
import { transformPool } from './helpers';

export const useFetchPublicPoolsData = () => {
    const dispatch = useAppDispatch();
    const { slowRefresh } = useRefresh();
    useEffect(() => {
        const fetchPoolsPublicData = async () => {
            const blockNumber = await simpleRpcProvider.getBlockNumber();
            dispatch(fetchPoolsPublicDataAsync(blockNumber));
        };
        fetchPoolsPublicData();
        dispatch(fetchPoolsStakingLimitsAsync())
    }, [dispatch, slowRefresh]);
};
export const useFetchUserPools = (account) => {
    const { fastRefresh } = useRefresh();
    const dispatch = useAppDispatch();
    useEffect(() => {
        if (account) {
            dispatch(fetchPoolsUserDataAsync(account));
        }
    }, [account, dispatch, fastRefresh]);
};
export const usePools = () => {
    const { pools, userDataLoaded } = useSelector((state) => ({
        pools: state.pools.data,
        userDataLoaded: state.pools.userDataLoaded,
    }));
    return { pools: pools.map(transformPool), userDataLoaded };
};

export const useCakeVault = () => {
    const { totalShares: totalSharesAsString, pricePerFullShare: pricePerFullShareAsString, totalCakeInVault: totalCakeInVaultAsString, estimatedCakeBountyReward: estimatedCakeBountyRewardAsString, totalPendingCakeHarvest: totalPendingCakeHarvestAsString, fees: { performanceFee, callFee, withdrawalFee, withdrawalFeePeriod }, userData: { isLoading, userShares: userSharesAsString, cakeAtLastUserAction: cakeAtLastUserActionAsString, lastDepositedTime, lastUserActionTime, }, } = useSelector((state) => state.pools.cakeVault);
    const estimatedCakeBountyReward = useMemo(() => new BigNumber(estimatedCakeBountyRewardAsString), [estimatedCakeBountyRewardAsString]);
    const totalPendingCakeHarvest = useMemo(() => new BigNumber(totalPendingCakeHarvestAsString), [totalPendingCakeHarvestAsString]);
    const totalShares = useMemo(() => new BigNumber(totalSharesAsString), [totalSharesAsString]);
    const pricePerFullShare = useMemo(() => new BigNumber(pricePerFullShareAsString), [pricePerFullShareAsString]);
    const totalCakeInVault = useMemo(() => new BigNumber(totalCakeInVaultAsString), [totalCakeInVaultAsString]);
    const userShares = useMemo(() => new BigNumber(userSharesAsString), [userSharesAsString]);
    const cakeAtLastUserAction = useMemo(() => new BigNumber(cakeAtLastUserActionAsString), [cakeAtLastUserActionAsString]);
    return {
        totalShares,
        pricePerFullShare,
        totalCakeInVault,
        estimatedCakeBountyReward,
        totalPendingCakeHarvest,
        fees: {
            performanceFee,
            callFee,
            withdrawalFee,
            withdrawalFeePeriod,
        },
        userData: {
            isLoading,
            userShares,
            cakeAtLastUserAction,
            lastDepositedTime,
            lastUserActionTime,
        },
    };
};
