import React, { useCallback, useEffect, useState } from 'react';
import { useWeb3React } from '@web3-react/core';
import { ethers } from 'ethers';
import { useAppDispatch } from '../../../redux/store';
import { updateUserAllowance } from '../../../state/actions';

import { useSousChef } from '../../../hooks/useContract';
import { useCallWithGasPrice } from '../../../hooks/useCallWithGasPrice';
import {showError, showFulfill} from "../../../config/helpers";

export const useApprovePool = (lpContract, sousId, earningTokenSymbol) => {
    const [requestedApproval, setRequestedApproval] = useState(false);
    const { callWithGasPrice } = useCallWithGasPrice();

    const dispatch = useAppDispatch();
    const { account } = useWeb3React();
    const sousChefContract = useSousChef(sousId);
    const handleApprove = useCallback(async () => {
        try {
            setRequestedApproval(true);
            const tx = await callWithGasPrice(lpContract, 'approve', [sousChefContract.address, ethers.constants.MaxUint256]);
            const receipt = await tx.wait();
            dispatch(updateUserAllowance(sousId, account));
            if (receipt.status) {
                showFulfill("Successfully Approved!")
                setRequestedApproval(false);
            }
            else {
                // user rejected tx or didn't go thru
                showError('Please try again. Confirm the transaction and make sure you are paying enough gas!');
                setRequestedApproval(false);
            }
        }
        catch (e) {
            console.error(e);
            showError('Please try again. Confirm the transaction and make sure you are paying enough gas!');
        }
    }, [
        account,
        dispatch,
        lpContract,
        sousChefContract,
        sousId,
        earningTokenSymbol,
        showFulfill,
        showError,
        callWithGasPrice,
    ]);
    return { handleApprove, requestedApproval };
};