import PropTypes from 'prop-types';
import {useMemo, useState} from 'react';
import {
    Button,
    Card, CardActions,
    CardContent,
    Grid,
    TextField
} from "@material-ui/core";

import {ethers} from "ethers";
import BigNumber from "bignumber.js";
import {escapeRegExp} from "../../../utils";
import useActiveWeb3React from "../../../hooks/useWeb3";
import {showError} from "../../../config/helpers";
import {getBalanceAmount, getFullDisplayBalance} from "../../../utils/formatBalance";

InputField.propTypes = {
    tokenSymbol: PropTypes.string.isRequired,
    isMax: PropTypes.bool,
    label: PropTypes.string,
    placeHolder: PropTypes.string,
    inputValue: PropTypes.number,
    onInputChange: PropTypes.func,
    onMax: PropTypes.func,
    selectedToken: PropTypes.any,
    tokenBalance: PropTypes.any,
    max: PropTypes.any,
    onConfirm: PropTypes.any,
    onDismiss: PropTypes.any
}

const inputRegex = RegExp(`^\\d*(?:\\\\[.])?\\d*$`)

export default function InputField(
    {
        tokenSymbol = "",
        isMax=false,
        label="",
        placeHolder="",
        inputValue="",
        onInputChange=null,
        onMax=null,
        selectedToken,
        tokenBalance,
        max,
        onConfirm,
        onDismiss
    }
) {
    const { account } = useActiveWeb3React()
    const [pendingTx, setPendingTx] = useState(false)

    const fullBalance = useMemo(() => getFullDisplayBalance(max), [max])

    const valNumber = new BigNumber(inputValue)
    const fullBalanceNumber = new BigNumber(fullBalance)

    const enforcer = (nextUserInput) => {
        if (nextUserInput === '' || inputRegex.test(escapeRegExp(nextUserInput))) {
            onInputChange(nextUserInput)
        }
    }

    const isBalanceZero = max === '0' || !max

    const displayBalance = (balance) => {
        try{
            const stakedBalanceBigNumber = getBalanceAmount(balance)
            if (isBalanceZero) {
                return '0'
            }
            if (stakedBalanceBigNumber.gt(0)) {
                return stakedBalanceBigNumber.toFixed(8, BigNumber.ROUND_DOWN)
            }
            return stakedBalanceBigNumber.toFixed(3, BigNumber.ROUND_DOWN)
        }catch (e) {
            showError(e.data ? e.data.message : e.message)
        }
    }

    return (
        <>
            <Card sx={{maxWidth: 620}}>
                <CardContent sx={{background: '#e7e9ec', margin: '5px', borderRadius:'inherit'}}>
                    <div>
                        <p>
                            {selectedToken
                                ? `Balance: ${displayBalance(tokenBalance) ?? 'Loading' } `
                                : ' - '}
                            {
                                (account && isMax && selectedToken) &&
                                <Button variant="outlined" style={{marginLeft:'3px'}} onClick={onMax} size="small">Max</Button>
                            }
                        </p>
                    </div>
                    <div style={{marginTop: '5px'}}>
                        <Grid container spacing={1}>
                            <Grid item xs={8} md={8}>
                                <TextField
                                    inputMode="decimal"
                                    step="any"
                                    min="0"
                                    type="number"
                                    fullWidth
                                    value={parseFloat(inputValue)}
                                    variant="filled"
                                    label={label}
                                    placeholder={placeHolder}
                                    title={label}
                                    onChange={(event) => {
                                        enforcer(event.currentTarget.value.replace(/,/g, '.'))
                                    }}
                                />
                            </Grid>
                            <Grid item xs={4} md={4} style={{fontSize:'10px', display: 'flex', alignItems:'center'}}>
                                <Button onClick={onMax} variant="outlined" style={{marginRight: '5px'}}>
                                    Max
                                </Button>
                                {tokenSymbol}
                            </Grid>
                        </Grid>
                    </div>
                </CardContent>
                <CardActions>
                    <Button
                        variant="contained"
                        onClick={
                            async () => {
                                setPendingTx(true)
                                try{
                                    await onConfirm(inputValue)
                                    onDismiss()
                                }catch(e){
                                    showError(e.data ? e.data.message : e.message)
                                }finally {
                                    setPendingTx(false)
                                }
                            }}
                        fullWidth
                        disabled={pendingTx || !valNumber.isFinite() || valNumber.eq(0) || valNumber.gt(fullBalanceNumber)}>
                        Confirm
                    </Button>
                </CardActions>
            </Card>
        </>
    )
}