import BigNumber from 'bignumber.js'
import PropTypes from 'prop-types';
import {Button, Grid} from "@material-ui/core";
import {PoolCategory} from "../../config/constants/types";
import {BIG_ZERO} from "../../utils/bigNumber";
import StakeAction from "./components/StakeAction";
import HarvestAction from "./components/HarvestAction";
import CompoundAction from "./components/CompoundAction";
import {getFullDisplayBalance} from "../../utils/formatBalance";
import {useERC20} from "../../hooks/useContract";
import {useApprovePool} from "../pool-mtpl/hooks/useApprove";

StakeActions.propTypes = {
    pool: PropTypes.any,
    isCompounding: PropTypes.bool,
    stakedBalance: PropTypes.any,
    timeStamp: PropTypes.any
}

function StakeActions ({
    pool,
    isCompounding=false,
    timeStamp="0.00"
}) {
    const { sousId, stakingToken, earningToken, harvest, poolCategory, userData, earningTokenPrice } = pool
    const isBnbPool = poolCategory === PoolCategory.BINANCE

    const allowance = userData?.allowance ? new BigNumber(userData.allowance) : BIG_ZERO
    const stakingTokenBalance = userData?.stakingTokenBalance ? new BigNumber(userData.stakingTokenBalance) : BIG_ZERO
    const stakedBalance = userData?.stakedBalance ? new BigNumber(userData.stakedBalance) : BIG_ZERO
    const earnings = userData?.pendingReward ? new BigNumber(userData.pendingReward) : BIG_ZERO
    const needsApproval = !allowance.gt(0) && !isBnbPool
    const isStaked = stakedBalance.gt(0)
    const isLoading = !userData
    const hasEarnings = earnings.toNumber() > 0
    const fullBalance = getFullDisplayBalance(stakedBalance, earningToken.decimals)
    const stakingTokenContract = useERC20(stakingToken.address || '')
    const { handleApprove, requestedApproval } = useApprovePool(stakingTokenContract, sousId, earningToken.symbol)
    const isDisabled = false;

    return (
        <>
            {/* <Grid container sx={{ py: 3, textAlign: 'center' }} spacing={3}>
                <Grid item xs={4} md={4}>
                    <HarvestAction hasEarnings={hasEarnings} isDisabled={isDisabled} needsApproval={needsApproval} isBnbPool={isBnbPool} pool={pool} />
                </Grid>
                <Grid item xs={4} md={4}>
                    {
                        needsApproval && <Button variant="contained" color="info" onClick={handleApprove}>
                            Approve
                        </Button>
                    }
                    {
                        !needsApproval && <StakeAction pool={pool}stakingTokenBalance={stakingTokenBalance} isDisabled={needsApproval} />
                    }
                </Grid>
                <Grid item xs={4} md={4}>
                    <StakeAction pool={pool} stakingTokenBalance={stakedBalance} isDisabled={needsApproval || !isStaked} isUnstake />
                </Grid>
            </Grid> */}

            <div className='flex items-center justify-between gap-4'>
                <div className='col-span-1'>
                    <HarvestAction hasEarnings={hasEarnings} isDisabled={isDisabled} needsApproval={needsApproval} isBnbPool={isBnbPool} pool={pool} />
                </div>
                <div className='col-span-1'>
                    {
                            needsApproval && <button variant="contained" color="info" onClick={handleApprove} className="px-2 md:px-4 py-2 md:py-3 text-center text-xs md:text-sm text-white bg-pink-600 rounded-lg font-medium hover:bg-sky-600 transition-all disabled:bg-opacity-50">
                                Approve
                            </button>
                        }
                        {
                            !needsApproval && <StakeAction pool={pool}stakingTokenBalance={stakingTokenBalance} isDisabled={needsApproval} />
                        }
                </div>
                <div className='col-span-1'>
                    <StakeAction pool={pool} stakingTokenBalance={stakedBalance} isDisabled={needsApproval || !isStaked} isUnstake />
                </div>
            </div>

            {
                isCompounding &&
                <div style={{paddingBottom: "10px"}}>
                    <CompoundAction pool={pool} hasEarnings={hasEarnings} needsApproval={needsApproval} isDisabled={isDisabled} fullBalance={fullBalance} />
                </div>
            }
        </>
    )
}

export default StakeActions