import { useCallback } from 'react';
import { useWeb3React } from '@web3-react/core';
import BigNumber from 'bignumber.js';
import { useAppDispatch } from '../../../redux/store';
import { updateUserStakedBalance, updateUserBalance } from '../../../state/actions';
import { stakeFarm } from '../../../utils/calls';
import { DEFAULT_TOKEN_DECIMAL, DEFAULT_GAS_LIMIT } from '../../../config/index';
import { BIG_TEN } from '../../../utils/bigNumber';
import { useMasterchef, useSousChef } from '../../../hooks/useContract';
import getGasPrice from '../../../utils/getGasPrice';

const options = {
    gasLimit: DEFAULT_GAS_LIMIT,
};
const sousStake = async (sousChefContract, amount, decimals = 18) => {
    const gasPrice = getGasPrice();
    const tx = await sousChefContract.deposit(new BigNumber(amount).times(BIG_TEN.pow(decimals)).toString(), {...options, gasPrice});
    const receipt = await tx.wait();
    return receipt.status;
};
const sousStakeBnb = async (sousChefContract, amount) => {
    const gasPrice = getGasPrice();
    const tx = await sousChefContract.deposit(new BigNumber(amount).times(DEFAULT_TOKEN_DECIMAL).toString(), {...options, gasPrice});
    const receipt = await tx.wait();
    return receipt.status;
};
const useStakePool = (sousId, isUsingBnb = false, isCompound=false) => {
    const dispatch = useAppDispatch();
    const { account } = useWeb3React();
    const masterChefContract = useMasterchef();
    const sousChefContract = useSousChef(sousId);
    const handleStake = useCallback(async (amount, decimals) => {
        await stakeFarm(masterChefContract, sousId, amount, isCompound);
        dispatch(updateUserStakedBalance(sousId, account));
        dispatch(updateUserBalance(sousId, account));
    }, [account, dispatch, isUsingBnb, masterChefContract, sousChefContract, sousId]);
    return { onStake: handleStake };
};
export default useStakePool;